import React, { Component } from 'react';
import {names, icons, quotes, rand, titles} from '../helpers';
import themes from './themes';
import ButterToast, { CinnamonSugar } from 'butter-toast';
import './style.css';

const raiseOne = (name, extra = {}) => {
    const quote = rand(quotes);

    ButterToast.raise(CinnamonSugar.crisp({
        toastTimeout: extra.randTimeout ? Math.round(Math.random() * 4000) + 3000 : 5000,
        name,
        message: quote,
        title: rand(titles),
        theme: rand(themes),
        icon: rand(icons),
    }), extra);

};

const wow = (rand, extra) => {
    names.forEach((name) => {
        const num = Math.round(Math.random()*2);
        for (let index = 0; index < num; index++) {
            raiseOne(name, extra);
        }
    });
}

const crazy = () => wow(true, {
    randTimeout: true
});

const raiseSticky = () => {
    raiseOne(rand(names), {
        sticky: true,
        dismissOnClick: true
    })
}

function Top() {
    return (
        <div className="app-top">
            <div className="content">
                <div>
                <h1 className="title">BUTTER TOAST</h1>
                <div className="sub">Smooth toast notification system for React</div>
                <div>
                    <a href="#!" className="btn-show" onClick={wow}>
                        Show me what you got
                    </a>
                    <a href="#!" className="btn-show" onClick={raiseSticky}>
                        And this one is sticky
                    </a>
                    <a href="#!" className="btn-show" onClick={crazy}>
                        Random Timeouts
                    </a>
                    </div>
                </div>
                <div>
                    <a href="https://github.com/ealush/butter-toast">Github</a> OR <a href="https://www.npmjs.com/package/butter-toast">NPM</a>
                </div>
            </div>
        </div>
    );
}

export default Top;


